import React from 'react';
import { Container } from 'react-bootstrap';

function Projects() {
    return (
        <Container>
            <p>
                This site was programmed using React hooks for the functionality and quick loading times, Bootstrap for the responsive layout, and Figma + ScrollMagic.io for the animation.
            </p>
            <p className="red-text">
                (The Projects portion under construction still as it takes time to port things to React)
            </p>
            <p className="green-text">
                Please view my projects in the left-hand panel!
            </p>    
        </Container>
    );
}
  
export default Projects;
  