import React, { 
    useState
} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faDiagramProject } from '@fortawesome/free-solid-svg-icons';
import { 
    Button, 
    Row, 
    Col, 
    Container 
} from 'react-bootstrap';
import '../css/app.css';
import ProjectThis from '../projects/this/Proj.js';
import ProjectSanAn from '../projects/puremaintenance/src/App.js';
import ProjectDV from '../projects/dvnametags/src/js/App.js';
//import ProjectRS from '../projects/rsgold/RSGold.js';

// lazy load spinner
const renderLoader = (
    <span className="sr-only">
        Loading...
    </span>
);

function Projects() {
    const projectListArray = [{
        i: 0,
        label: "This Site",
        pageContent: ProjectThis,
        desc: "",
    },{
        i: 1,
        label: "Deer Valley",
        pageContent: ProjectDV,
        desc: "An internal website for Deer Valley Ski Resort Employees to order nametags.",
    },{
        i: 2,
        label: "San Antonio",
        pageContent: ProjectSanAn,
        desc: "A site I made for my uncle with his Mold Remediation business.",
    }/*,{
        i: 3,
        label: "Runescape",
        pageContent: ProjectRS,
        desc: "",
    },{
        i: 4,
        label: "Artisan Zero",
        pageContent: ,
        desc: "",
    }*/];

    // dynamic description
    const[description, setDescription] = useState("");

    // button active styling
    const[btnActiveIndex, setBtnActiveIndex] = useState(0);

    // dynamically load page that is clicked on from the above array
    const[activePage, setActivePage] = useState(ProjectThis);
    const changeActivePage = (pageContent, description, indexOfElement) => {
        setActivePage(pageContent);
        setDescription(description);
        setBtnActiveIndex(indexOfElement);
    };

    return (
        <Container id="project-panel">
            <Row className="align-items-center">
                <FontAwesomeIcon size="md" icon={faDiagramProject} />
                <h2>Projects &nbsp;&#10097;&#10097;&nbsp;</h2>
                <p className="mt-2">{description}</p>
            </Row>
            <Row>
                <Col xs={2} id="project-list-parent">
                {
                    projectListArray.map((item, index) => 
                        <Row>
                            <Button onClick={() => changeActivePage(item.pageContent, item.desc, item.i)} variant="outline-secondary" className={"w-100 mt-2 pt-2 pb-0" + (index === btnActiveIndex ? " active" : "")}>
                                <h4>
                                    {item.label}
                                </h4>
                            </Button>
                        </Row>
                    )
                }
                </Col>
                <Col xs={10} id="active-page-parent" className="p-0">
                    {activePage}
                </Col>
            </Row>
        </Container>
    );
}
  
  export default Projects;
  